import React from "react";
import { MDBJumbotron, MDBBtn, MDBIcon, MDBCardBody, MDBCardText, MDBCardTitle } from "mdbreact";

const Jumbotron = () => (
  <>
    <section>
      <MDBJumbotron className="text-center mx-auto p-3 my-5">
        <MDBCardBody>
          <MDBCardTitle>Регистрации и&nbsp;оплата участия</MDBCardTitle>
          <p className="custom-green-text my-4 font-weight-bold">Участие в&nbsp;Олимпиаде&nbsp;&mdash; добровольное и&nbsp;платное</p>
          <MDBCardText>
            Для участия в&nbsp;Олимпиаде необходимо ознакомится с у&nbsp;условиями
        </MDBCardText>
          <hr className="my-4" />
          <div className="pt-2">
            <MDBBtn outline color="green">
              Инструкция <MDBIcon icon="info-circle" />
            </MDBBtn>
            <MDBBtn color="green">
              Оплатить <MDBIcon far icon="credit-card" />
            </MDBBtn>
          </div>
        </MDBCardBody>
      </MDBJumbotron>
    </section>
  </>
)

export default Jumbotron