import React from "react"
import { MDBTypography, MDBCard, MDBCardBody, MDBCardText, MDBCardHeader, MDBIcon, MDBRow, MDBCol } from "mdbreact"

const Details = () => (
  <>
    <section className="details">
      <MDBTypography tag="h2" variant="h2-responsive" className="text-center">
        Программа проведения олимпиады
      </MDBTypography>
      <MDBRow>
        <MDBCol sm="6" className="my-3">
          <MDBCard className="h-100">
            <MDBCardHeader color="light-green lighten-3" className="custom-dark-text">
              <MDBIcon icon="map-marker-alt" className="red-text mr-2" /> Место
            </MDBCardHeader>
            <MDBCardBody>
              <MDBCardText>
                Олимпиада будет проведена дистанционно <span className="d-block">(в режиме он - лайн) на интернет &mdash; портале
                &laquo;ЯКласс&raquo;</span>
          </MDBCardText>
            </MDBCardBody>
          </MDBCard>
        </MDBCol>
        <MDBCol sm="6" className="my-3">
          <MDBCard className="h-100">
            <MDBCardHeader color="lime lighten-3" className="custom-dark-text">
              <MDBIcon icon="clock" className="deep-orange-text mr-2" /> Время
            </MDBCardHeader>
            <MDBCardBody>
              <MDBCardText>
                <ul>
                  <li>&laquo;21&raquo; марта 2020 года</li>
                  <li>в период с 11.00 до 12.00</li>
                </ul>
              </MDBCardText>
            </MDBCardBody>
          </MDBCard>
        </MDBCol>
      </MDBRow>
    </section>
  </>
)

export default Details
