import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import { MDBContainer, MDBView, MDBMask, MDBTypography } from "mdbreact"

const Prizes = () => {
  const data = useStaticQuery(graphql`
  query {
    prizeBg: file(relativePath: { eq: "children_bg.jpeg" }) {
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`)

  return (
    <>
      <section className="prizes mt-5">
        <MDBView className="prizes__wrapper">
          <Img fluid={data.prizeBg.childImageSharp.fluid} />
          <MDBMask className="flex-center" overlay="teal-strong">
            <MDBContainer size="lg" className="white-text custom-headers-block">
              <MDBTypography tag="h2" variant="h3-responsive">
                Победитель Олимпиады, занявший первое  место , награждается  смартфоном!
              </MDBTypography>
              <MDBTypography tag="h2" variant="h3-responsive">
                Участники , занявшие 2 и 3 места – ценными призами!
              </MDBTypography>
              <MDBTypography tag="h2" variant="h3-responsive">
                Все участники Олимпиады получают почетные дипломы!
              </MDBTypography>
            </MDBContainer>
          </MDBMask>
        </MDBView>
      </section>
    </>
  )
}

export default Prizes